import React from 'react'
import {Modal, Stack, Form, Button} from 'react-bootstrap'
import aadirPubli from '../Componentes/AadirPubli'

export default function ModalAadir({isModalAadir, setIsModalAadir, actualizarPubli}) {

    function aadirPubliModal () {
        //obtenemos la informacin del formulario
        const titulo = document.getElementById('titulo').value;
        const ubicacin = document.getElementById('ubicacin').value;
        const descripcin = document.getElementById('descripcin').value;
        const Fecha = document.getElementById('Fecha').value;
        const id = document.getElementById('id').value;
        //se envia la informacinn a firebase
        const infoPubli = {titulo, ubicacin, descripcin, Fecha, id};
        aadirPubli(infoPubli);
        //cerramos el modal
        actualizarPubli();
        setIsModalAadir(false);
    }

  return (
    <Modal show={isModalAadir} onHide={() => setIsModalAadir(false)}>
        <Modal.Header>
            <Modal.Title>Aadir Publicacin</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Stack>
                    <Form.Control id='titulo' placeholder='Titulo' type='text' className='mb-1'/>
                    <Form.Control id='ubicacin' placeholder='Ubicacin' type='text' className='mb-1' />
                    <Form.Control id='descripcin' placeholder='Descripcin' type='text' className='mb-1' />
                    <Form.Control id='Fecha' placeholder='Fecha' type='text' className='mb-1' />
                    <Form.Control id='id' placeholder='ID' type='text' className='mb-1' />
                </Stack>
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button  variant='light' onClick={() => setIsModalAadir(false)}>Cancelar</Button>
            <Button  onClick={aadirPubliModal} variant='success'>Aadir</Button>
        </Modal.Footer>
    </Modal>
  )
}
