import React, {useState} from 'react'
import {Modal, Stack, Form, Button} from 'react-bootstrap'
import aadirPubli from '../Componentes/AadirPubli'

export default function ModalEditar({isModalEditar, setIsModalEditar, actualizarPubli, publiEditar, setPubliEditar}) {

    function editarPubliModal () {
        //obtenemos la informacin del formulario
        const titulo = document.getElementById('titulo').value;
        const ubicacin = document.getElementById('ubicacin').value;
        const descripcin = document.getElementById('descripcin').value;
        const Fecha = document.getElementById('Fecha').value;
        const id = document.getElementById('id').value;
        //se envia la informacinn a firebase
        const infoPubli = {titulo, ubicacin, descripcin, Fecha, id};
        aadirPubli(infoPubli);
        //cerramos el modal //regresar el estado a {false}
        setPubliEditar(null);
        actualizarPubli();
        setIsModalEditar(false);
    }
    
    const [publiEstado, setPubliEstado] = useState({...publiEditar});

    return (
    <Modal show={isModalEditar} onHide={() => {
        setIsModalEditar(false);
        setPubliEditar(null);
      }}>
        <Modal.Header>
            <Modal.Title>Editar Publicacin</Modal.Title>
        </Modal.Header>
        <Modal.Body>
            <Form>
                <Stack>
                    <Form.Control id='titulo' placeholder='Titulo' type='text' className='mb-1'
                    value={publiEstado.titulo} onChange={(e)=> setPubliEstado({...publiEstado, titulo: e.target.value})} />
                    <Form.Control id='ubicacin' placeholder='Ubicacin' type='text' className='mb-1' 
                    value={publiEstado.ubicacin} onChange={(e)=> setPubliEstado({...publiEstado, ubicacin: e.target.value})} />
                    <Form.Control id='descripcin' placeholder='Descripcin' type='text' className='mb-1' 
                    value={publiEstado.descripcin} onChange={(e)=> setPubliEstado({...publiEstado, descripcin: e.target.value})} />
                    <Form.Control id='Fecha' placeholder='Fecha' type='text' className='mb-1'
                    value={publiEstado.Fecha} onChange={(e)=> setPubliEstado({...publiEstado, Fecha: e.target.value})}  />
                    <Form.Control id='id' placeholder='ID' type='text' className='mb-1' 
                    value={publiEstado.id} onChange={(e)=> setPubliEstado({...publiEstado, id: e.target.value})} />
                </Stack>
            </Form>
        </Modal.Body>
        <Modal.Footer>
            <Button  variant='light' onClick={() => {
            setIsModalEditar(false);
            setPubliEditar(null);
          }}>Cancelar</Button>
            <Button  onClick={editarPubliModal} variant='success'>Editar</Button>
        </Modal.Footer>
    </Modal>
  )
}